.allMeasuresContainer{
	background: white;
	width: 1200px;
	margin: 0 auto 120px auto;
	padding-bottom: 13px;
	-webkit-box-shadow: -18px 22px 40px -15px #212121;
	        box-shadow: -18px 22px 40px -15px #212121;
}

.allMeasuresContainer button{
	color: white;
	/*border: 1px solid transparent;*/
	font-size: 1em;
	text-align: center;
	/*width: 100%;*/
	border-radius: 8px;
	background: #212121;
	border: 0;
}

.allMeasuresContainer button:hover{
	color: white;
    background: -webkit-gradient(linear, left top, right top, from(#993333), to(#212121));
    background: -webkit-linear-gradient(left, #993333, #212121);
    background: -o-linear-gradient(left, #993333, #212121);
    background: linear-gradient(to right, #993333, #212121);
	cursor: pointer;
}

.allMeasuresContainer button:focus{
	outline: none;
}


