.WTWrapper{
	margin: 0 auto 10px auto;
	clear:both;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	width: 100%;
}

.measureButtonContainer{
	margin-left: 15px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.measure{
	width: 1030px;
}

.WTWrapper button{
	margin-left: auto;
	margin-right: auto;
}

.WTWrapper button:first-child{
	margin-bottom: 7px;
}

.WTWrapper button:last-child{
	margin-top: 7px;
}


