.noteSelectorContainer {
	margin-bottom: 20px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	width: 60%;
	margin-left: auto;
	margin-right: auto;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
}

.noteOption {
	height: 70px;
	padding: 10px;
	border: 2px solid transparent;
	cursor: pointer;
	-webkit-filter: invert(100%);
	        filter: invert(100%);
}

.solidBorder {
	border-bottom: 2px solid #000;
	-webkit-filter: invert(100%) drop-shadow(-5px -3px 4px #212121);
	        filter: invert(100%) drop-shadow(-5px -3px 4px #212121);
}

.noteOption:hover{
	-webkit-filter: invert(100%) drop-shadow(-5px -3px 4px #212121);
	        filter: invert(100%) drop-shadow(-5px -3px 4px #212121);
	border-bottom: 2px solid rgba(0,0,0,1)
	/*background-color: rgba(255,255,255,.1);*/
}


